* {
  font-size: 16px;
  font-family: "robotoregular";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

blockquote p {
  font-family: "robotolight_italic"; }

cite {
  font-family: "robotobold_italic"; }
  cite:before {
    content: "\2014"; }

a {
  font-family: "robotomedium";
  color: #6da3bd;
  text-decoration: underline; }
  a:hover {
    text-decoration: none;
    cursor: pointer; }

ul {
  list-style-type: none;
  padding: 0;
  margin: 0; }

h1, h2, h3 {
  font-weight: 300; }

.visuallyHidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 0.0625rem;
  width: 0.0625rem;
  margin: -0.0625rem;
  padding: 0;
  border: 0;
  opacity: 0; }

body {
  position: relative; }

.wellcome-page {
  background-image: url("../../img/main_bg.jpg");
  background-size: cover;
  height: 100vh;
  position: relative; }

.container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 95vh;
  position: relative; }

.auth-button {
  position: absolute;
  top: 25px;
  right: 60px;
  width: 180px;
  height: 45px;
  border-radius: 25px;
  background-color: transparent;
  border: 1px solid #fff;
  color: #fff;
  font-family: "robotomedium";
  transition: .2s; }
  .auth-button:hover {
    background-color: #4e8839;
    border-color: #538643;
    cursor: pointer; }

.auth {
  margin: 0 auto; }
  .auth-intro {
    border-radius: 5px;
    width: 380px;
    height: 395px;
    display: flex;
    flex-direction: column;
    background-color: rgba(18, 38, 32, 0.6);
    overflow: hidden; }

.avatar {
  width: 140px;
  height: 140px;
  border-radius: 50%;
  overflow: hidden; }

.avatar__pic {
  width: 140px;
  height: 140px; }

.auth-intro__avatar {
  margin: 38px auto 20px; }

.auth-intro__name {
  color: #fff;
  font-size: 28px;
  font-family: "robotomedium";
  line-height: 34px;
  font-smoothing: antialiased;
  margin: 0 auto 10px; }

.auth-intro__descr {
  color: #fff;
  opacity: .8;
  font-family: "robotomedium";
  margin: 0 auto 35px; }

.auth-intro__socials {
  margin-bottom: 40px; }

.socials {
  width: 140px; }
  .socials__index {
    margin: 0 auto; }
  .socials__top {
    width: 138px;
    margin: 0 0 0 30px; }

.social__list {
  display: flex; }

.social__item {
  margin-right: 25px; }
  .social__item:last-child {
    margin-right: 0; }

.icon {
  height: 22px;
  width: 30px;
  transition: .2s; }
  .icon.standard-mod {
    fill: #8e9c99; }
  .icon:hover {
    fill: #4e8839;
    cursor: pointer; }

.auth-intro__buttons {
  height: 45px;
  background-color: red;
  border-radius: 0 0 5px 5px; }

.buttons__list {
  display: flex;
  justify-content: space-between; }

.buttons__link {
  display: block;
  height: 100%;
  color: #fff;
  text-decoration: none;
  text-align: center;
  line-height: 45px; }

.buttons__item {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin: 0 auto;
  height: 45px;
  background-color: #6c9c5a;
  transition: .2s; }
  .buttons__item:hover {
    background-color: #538643;
    cursor: pointer; }
  .buttons__item:nth-child(1) {
    width: 154px;
    border-right: 2px solid #a6bda3;
    border-radius: 0 0 0 5px; }
  .buttons__item:nth-child(2) {
    width: 127px;
    border-right: 2px solid #a6bda3; }
  .buttons__item:nth-child(3) {
    width: 97px;
    border-radius: 0 0 5px 0; }
  .buttons__item span {
    display: block;
    color: #fff;
    text-align: center;
    font-family: "robotomedium"; }

.wellcome-footer {
  color: #fff;
  margin: 0 auto;
  display: flex;
  justify-content: space-around; }



